.skill {
    display: flex;
    flex-direction: column;
    align-items: center;
    opacity: 1;
    height: auto;
    animation-duration: 3s;
    animation-fill-mode: both;

    .icon {

    }

    & h4 {
        color: #fff;
        font-family: Poppins, sans-serif;
        margin: 15px 0 9px;
        font-size: 20px;
        font-weight: 500;
        line-height: 1.1;
    }

    & p {
        font-family: Poppins, sans-serif;
        color: #eee;
        font-size: 12px;
        font-weight: 300;
        line-height: 1.5;
        margin-bottom: 20px;
    }
}
.left {
    animation-name: inLeft;
}
.right {
    animation-name: inRight;
}
.top {
    animation-name: inTop;
}
.bottom {
    animation-name: inBottom;
}



.description {
    text-align: center;
}

@keyframes inLeft {
    0% {
        opacity: 0;
        transform: translate3d(-100%,0,0);
    }
    100% {
        opacity: 1;
        transform: translateZ(0);
    }
}
@keyframes inRight {
    0% {
        opacity: 0;
        transform: translate3d(100%,0,0);
    }
    100% {
        opacity: 1;
        transform: translateZ(0);
    }
}
@keyframes inTop {
    0% {
        opacity: 0;
        transform: translate3d(0,-100%,0);
    }
    100% {
        opacity: 1;
        transform: translateZ(0);
    }
}
@keyframes inBottom {
    0% {
        opacity: 0;
        transform: translate3d(0,100%,0);
    }
    100% {
        opacity: 1;
        transform: translateZ(0);
    }
}