.item {
    width: 200px;
    height: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    animation-duration: 1s;
    animation-fill-mode: both;
    animation-name: fadeIn;

    .coins {
        & span {
            color: #fff;
            font-size: 30px;
        }

        .symbol {
            position: absolute;
            font-size: 17px;
            color: #eb83f8;
        }
    }

    .subscribe {
        color: #fff;
        font-size: 14px;
        line-height: 15px;
        font-family: Poppins, sans-serif;
        font-weight: 300;
    }
}



@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}